// @ts-nocheck
import Vue from "vue";
import { createApp } from "vue";
import "./engine/plugins/vuetify";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./registerServiceWorker";
import "roboto-fontface/css/roboto/roboto-fontface.css";
import "font-awesome/css/font-awesome.css";
import 'material-design-icons-iconfont/dist/material-design-icons.css';
import cm from "@/engine/ComponentManager";
import bm from "@/engine/BoardManager";
import ui from "@/engine/UIManager";
import pfm from "@/engine/PlatformManager";
import pm from "@/engine/PluginManager";
import compiler from "@/engine/Compiler";
import util from "@/engine/utils";
import mitt from 'mitt';
const { promises: fs } = require("fs");
const checkFileExists = async (path) => !!(await fs.stat(path).catch(e => false));
const DirectusSDK = require('@directus/sdk-js');
const directus_client = DirectusSDK({
    url: "https://manage.kbide.org/",
    project: "_",
    storage: window.localStorage
});
const directus_dev_client = DirectusSDK({
    url: "https://manage.kbide.org/",
    project: "_",
    storage: window.localStorage
});
(async () => {
    const VueAny = /** @type {any} */ (Vue);
    VueAny.prototype.$db_dev = directus_dev_client;
    VueAny.prototype.$db2 = directus_client;
    const emitter = mitt();
    VueAny.prototype.$global = new Vue({
        data: (cm && cm.data) ? cm.data : {},
        methods: { $on: emitter.on, $off: emitter.off, $emit: emitter.emit }
    });
    const hideLoading = () => {
        try {
            const el = document.querySelector('.loading');
            if (el) {
                el.style.display = 'none';
            }
        }
        catch (e) { }
    };
    if (window.config && window.config.mode) {
        window.config.mode = parseInt(window.config.mode);
        VueAny.prototype.$global.editor.mode = window.config.mode;
    }
    if (window.config && window.config.file && await checkFileExists(window.config.file)) {
        let targetFile = window.config.file;
        if (targetFile.endsWith(".bly")) {
            let contentFile = await fs.readFile(targetFile, "utf8");
            VueAny.prototype.$global.editor.blockCode = util.b64DecodeUnicode(contentFile);
        }
        else if (window.config.file.endsWith(".ino")) {
            VueAny.prototype.$global.editor.sourceCode = await fs.readFile(targetFile, "utf8");
        }
    }
    let engineData = { util, compiler, componentManager: cm, boardManager: bm, platformManager: pfm, uiManager: ui, pluginManager: pm };
    VueAny.prototype.$engine = new Vue({ data: () => engineData });
    const app = createApp(App);
    app.config.globalProperties.$global = VueAny.prototype.$global;
    app.config.globalProperties.$engine = VueAny.prototype.$engine;
    app.config.globalProperties.$db_dev = VueAny.prototype.$db_dev;
    app.config.globalProperties.$db2 = VueAny.prototype.$db2;
    app.config.globalProperties.$track = VueAny.prototype.$track;
    app.provide('global', VueAny.prototype.$global);
    app.provide('engine', VueAny.prototype.$engine);
    app.provide('db', VueAny.prototype.$db2);
    app.provide('db_dev', VueAny.prototype.$db_dev);
    app.provide('track', VueAny.prototype.$track);
    app.use(router);
    app.use(store);
    app.mount('#app');
    hideLoading();
})().catch(err => { console.error(err); });
